"use client";

import { useEffect, useMemo, useState } from "react";
import Image from "next/image";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Input } from "@/components/ui/input";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";

import {
  Product,
  addProduct,
  deleteProduct,
  loadProducts,
  seedProductsIfEmpty,
} from "@/lib/store";

function fileToBase64(file: File): Promise<string> {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = () => resolve(reader.result as string);
    reader.onerror = reject;
    reader.readAsDataURL(file);
  });
}

export default function ProductsPage() {
  const [products, setProducts] = useState<Product[]>([]);
  const [open, setOpen] = useState(false);

  const [sku, setSku] = useState("");
  const [name, setName] = useState("");
  const [unit, setUnit] = useState("ชิ้น");
  const [minStock, setMinStock] = useState(0);
  const [image, setImage] = useState<string | undefined>(undefined);

  useEffect(() => {
    seedProductsIfEmpty();
    setProducts(loadProducts());
  }, []);

  const refresh = () => setProducts(loadProducts());

  const canSave = useMemo(() => sku.trim() && name.trim(), [sku, name]);

  const onAdd = () => {
    if (!canSave) return;

    addProduct({
      sku: sku.trim(),
      name: name.trim(),
      unit: unit.trim() || "ชิ้น",
      minStock: Number(minStock) || 0,
      image,
    });

    setSku("");
    setName("");
    setUnit("ชิ้น");
    setMinStock(0);
    setImage(undefined);

    setOpen(false);
    refresh();
  };

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold">สินค้า</h1>
          <p className="text-sm text-slate-500">
            เพิ่ม/ลบสินค้า + อัปโหลดรูปสินค้าได้ (LocalStorage)
          </p>
        </div>

        <Dialog open={open} onOpenChange={setOpen}>
          <DialogTrigger asChild>
            <Button className="rounded-xl">+ เพิ่มสินค้า</Button>
          </DialogTrigger>

          <DialogContent className="rounded-2xl">
            <DialogHeader>
              <DialogTitle>เพิ่มสินค้า</DialogTitle>
            </DialogHeader>

            <div className="space-y-3">
              <div>
                <div className="text-sm font-medium">SKU</div>
                <Input
                  value={sku}
                  onChange={(e) => setSku(e.target.value)}
                  placeholder="เช่น P004"
                  className="mt-2"
                />
              </div>

              <div>
                <div className="text-sm font-medium">ชื่อสินค้า</div>
                <Input
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  placeholder="เช่น น้ำตาล"
                  className="mt-2"
                />
              </div>

              <div>
                <div className="text-sm font-medium">หน่วย</div>
                <Input
                  value={unit}
                  onChange={(e) => setUnit(e.target.value)}
                  placeholder="เช่น ขวด/ชิ้น/แพ็ค"
                  className="mt-2"
                />
              </div>

              <div>
                <div className="text-sm font-medium">ขั้นต่ำ</div>
                <Input
                  value={minStock}
                  onChange={(e) => setMinStock(Number(e.target.value))}
                  type="number"
                  className="mt-2"
                />
              </div>

              <div>
                <div className="text-sm font-medium">รูปสินค้า</div>
                <input
                  type="file"
                  accept="image/*"
                  className="mt-2 block w-full text-sm"
                  onChange={async (e) => {
                    const file = e.target.files?.[0];
                    if (!file) return;

                    // จำกัดขนาดกัน localStorage ระเบิด
                    if (file.size > 800_000) {
                      alert("รูปใหญ่เกินไป (เกิน 800KB) กรุณาลดขนาดรูปก่อน");
                      return;
                    }

                    const base64 = await fileToBase64(file);
                    setImage(base64);
                  }}
                />

                {image && (
                  <div className="mt-3 flex items-center gap-3">
                    <div className="relative h-16 w-16 overflow-hidden rounded-xl border bg-white">
                      <Image src={image} alt="preview" fill className="object-cover" />
                    </div>
                    <Button
                      variant="secondary"
                      onClick={() => setImage(undefined)}
                    >
                      ลบรูป
                    </Button>
                  </div>
                )}
              </div>

              <Button
                className="w-full rounded-xl"
                onClick={onAdd}
                disabled={!canSave}
              >
                บันทึก
              </Button>
            </div>
          </DialogContent>
        </Dialog>
      </div>

      <Card className="rounded-2xl">
        <CardContent className="p-4">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>รูป</TableHead>
                <TableHead>SKU</TableHead>
                <TableHead>ชื่อสินค้า</TableHead>
                <TableHead className="text-right">คงเหลือ</TableHead>
                <TableHead>หน่วย</TableHead>
                <TableHead className="text-right">ขั้นต่ำ</TableHead>
                <TableHead>สถานะ</TableHead>
                <TableHead className="text-right">จัดการ</TableHead>
              </TableRow>
            </TableHeader>

            <TableBody>
              {products.map((p) => {
                const low = p.stock <= p.minStock;
                return (
                  <TableRow key={p.id}>
                    <TableCell>
                      <div className="relative h-10 w-10 overflow-hidden rounded-xl border bg-white">
                        {p.image ? (
                          <Image src={p.image} alt={p.name} fill className="object-cover" />
                        ) : (
                          <div className="flex h-full w-full items-center justify-center text-[10px] text-slate-400">
                            No image
                          </div>
                        )}
                      </div>
                    </TableCell>
                    <TableCell className="font-medium">{p.sku}</TableCell>
                    <TableCell>{p.name}</TableCell>
                    <TableCell className="text-right font-semibold">{p.stock}</TableCell>
                    <TableCell>{p.unit}</TableCell>
                    <TableCell className="text-right">{p.minStock}</TableCell>
                    <TableCell>
                      {low ? (
                        <Badge variant="destructive">ใกล้หมด</Badge>
                      ) : (
                        <Badge variant="secondary">ปกติ</Badge>
                      )}
                    </TableCell>
                    <TableCell className="text-right">
                      <Button
                        variant="destructive"
                        size="sm"
                        onClick={() => {
                          if (!confirm(`ลบสินค้า: ${p.name}?`)) return;
                          deleteProduct(p.id);
                          refresh();
                        }}
                      >
                        ลบ
                      </Button>
                    </TableCell>
                  </TableRow>
                );
              })}

              {products.length === 0 && (
                <TableRow>
                  <TableCell colSpan={8} className="text-center text-slate-500">
                    ยังไม่มีสินค้า
                  </TableCell>
                </TableRow>
              )}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </div>
  );
}
